.section {
  width: 608px;
}

.section_big {
  width: 860px;
}

.scrollArea {
  max-height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
}

.scrollArea::-webkit-scrollbar-track {
  background-color: #2f2f37;
}

.scrollArea::-webkit-scrollbar {
  width: 8px;
}

.scrollArea::-webkit-scrollbar-thumb {
  background-color: #8585ad;
}

.card {
  cursor: default;
  opacity: 1;
  transition: opacity 0.2s ease-in-out;
  text-decoration: none;
  color: #ffffff;
}

.card:hover {
  cursor: pointer;
  opacity: 0.8;
}
